		"image": "economie-finance",
		"pos": 7,
		"intro": "<a href=\"http://www.europe1.fr/politique/sur-tf1-le-pen-en-candidate-du-peuple-contre-le-candidat-de-loligarchie-3311654\">Face  E.Macron, M. Le Pen aimait se prsenter</a> comme la  candidate du peuple  luttant contre contre  le rgne de l&#39;argent, l&#39;ide que le march est le patron , contre  l&#39;oligarchie ,  la finance  ou la  mondialisation sauvage .<br>Depuis la crise de 2008, le Front National a fait mine de changer, prnant un tat fort face  la mondialisation et la drgulation. Pourtant, dans les faits, alors que Jean-Marie Le Pen se prsentait comme  le Reagan Franais ,<a href=\"http://bfmbusiness.bfmtv.com/france/comment-le-fn-est-passe-de-l-ultra-liberalisme-a-l-etat-protecteur-933363.html\">le FN a juste mis un peu d&#39;eau dans son vin</a>. Dans son programme conomique, bien peu de  finance , d&#39; oligarchie , de  march  ou de  patron , mais des recettes conomiques finalement trs librales, comme la <a href=\"https://www.marine2017.fr/wp-content/uploads/2017/02/texte-chiffrage-version-finale-.pdf\">baisse des recettes de l&#39;tat (impts et taxes) et des dpenses publiques</a>. Accompagnes d&#39;une forte dose de  patriotisme conomique  et de protectionnisme pour dfendre le capital franais, mais pas vraiment les travailleurs, ni lcologie.",
