		"image": "immigration-laicite",
		"fn": " [9] Dfendre les droits des femmes : lutter contre lislamisme qui fait reculer leurs liberts fondamentales ().<br> [11] Garantir la libert de scolariser ses enfants selon ses choix (...).<br> [29] Interdire et dissoudre les organismes de toute nature lis au fondamentalistes islamistes (...).<br> [30] Fermer toutes les mosques extrmistes recenses par le ministre de lIntrieur (...). Interdire tout financement public () des lieux de culte et des activits cultuelles.<br> [91] Dfendre lidentit nationale, les valeurs et les traditions de la civilisation franaise. Inscrire dans la Constitution la dfense et la promotion de notre patrimoine historique et culturel.<br> [95] Promouvoir la lacit et lutter contre le communautarisme ().<br> [98] Promouvoir lassimilation rpublicaine, principe plus exigeant que celui dintgration.",
		"fi": "Pour M. Le Pen, <a href=\"http://www.rtl.fr/girls/identites/marine-le-pen-le-fn-applique-t-il-ses-discours-sur-l-egalite-hommes-femmes-7787275502\"> si nous perdons, le voile sera impos  toutes les femmes, la charia remplacera notre Constitution, la barbarie s&#39;installera </a>. Sur les prires de rue de musulmans, elle parlait d&#39; une <a href=\"http://www.lefigaro.fr/politique/2010/12/11/01002-20101211ARTFIG00475-islam-et-occupation-la-provocation-de-marine-le-pen.php\"> occupation de territoire  et voquait la  seconde guerre mondiale </a>. Mais la loi de 1905 n&#39;interdit pas les prires de rue, et le <a href=\"http://www.humanite.fr/pourquoi-marine-le-pen-ne-condamne-pas-les-prieres-de-rue-des-catholiques-integristes\">FN n&#39;a jamais condamn</a> celles de catholiques intgristes comme <a href=\"http://www.huffingtonpost.fr/2013/01/29/linstitut-civitas-fait-priere-rue-devahnt-assemblee-nationale-contre-mariage-gay_n_2575468.html\"> Civitas </a>.<br>Le FN souhaite <a href=\"http://www.famillechretienne.fr/politique-societe/presidentielle-2017/marine-le-pen-certains-catholiques-aiment-se-faire-peur-214377\">largir  l&#39;espace public la loi interdisant les signes religieux  l&#39;cole</a>, mais pour sa candidate, <a href=\"http://www.la-croix.com/Religion/France/La-laicite-geometrie-variable-Marine-Le-Pen-2017-02-13-1200824424\"> les religions chrtiennes nont pas de signes ostensibles </a>. Pour elle, dans le  patrimoine historique et culturel  de la France, <a href=\"http://www.famillechretienne.fr/politique-societe/presidentielle-2017/marine-le-pen-certains-catholiques-aiment-se-faire-peur-214377\"> les racines chrtiennes ont videmment un poids important </a>, et les crches <a href=\"https://www.youtube.com/watch?v=NgDx6yRa2fM\"> ne sont pas des symboles religieux , mais des  traditions </a>.</p>\n<p>Les droits des femmes n&#39;intressent le FN que quand il s&#39;agit d&#39;islamisme. M. Le Pen le dit clairement : <a href=\"http://www.rtl.fr/girls/identites/marine-le-pen-le-fn-applique-t-il-ses-discours-sur-l-egalite-hommes-femmes-7787275502\"> Je ne sais pas ce que a recouvre, le fminisme. Ce que je vois c&#39;est qu&#39;un certain nombre de droits des femmes sont en train de reculer, notamment sous la pousse du fondamentalisme islamiste (...) </a>.<br>En soutenant  la libert de scolariser ses enfants, le FN affiche son soutien  la loi Debr qui permet le financement public des tablissements privs (<a href=\"https://blogs.mediapart.fr/emancipation/blog/030413/financement-public-de-lenseignement-prive-lomerta-continue\">7 milliards d&#39;euros en 2013</a>), alors que <a href=\"http://www.laligue.org/wp-content/uploads/2017/03/Educ4_Liberte%CC%81Enseignement_Zoom3.pdf\">97,5% de ces tablissements sous contrat</a> sont catholiques. Le FN ne remet pas non plus en cause le <a href=\"https://fr.wikipedia.org/wiki/Concordat_en_Alsace-Moselle\"> concordat  en Alsace/Moselle</a>, ou la loi de sparation de l&#39;Eglise et de l&#39;Etat ne s&#39;applique pas.</p>\n<p>Quant  l&#39;interdiction et la dissolution des  organismes lis aux fondamentalistes , <a href=\"http://www.associations.gouv.fr/1006-dissoudre-une-association.html#cas-8f2a79-4\">elle est dj possible</a>, et <a href=\"http://www.la-croix.com/Religion/Islam/Dissolution-de-trois-associations-cultuelles-liees-a-la-mosquee-de-Lagny-2016-01-13-1404007\">plusieurs associations culturelles l&#39;ont t ces dernires annes</a>. Idem pour la fermeture des  mosques extrmistes  (<a href=\"http://www.rtl.fr/actu/societe-faits-divers/terrorisme-etat-d-urgence-operation-sentinelle-un-bilan-mitige-pour-des-mesures-critiquees-7783973780\">12 fermes en 2015</a>), ou pour <a href=\"http://www.slate.fr/story/121757/infographie-lieux-de-culte-france\">l&#39;interdiction du  financement public des lieux de culte </a>.</p>\n<p>Le FN ne s&#39;intresse donc pas vraiment  la lacit, ni au fminisme, mais si il met en avant ces thmes, c&#39;est avant tout pour s&#39;attaquer aux musulmans et aux banlieues.",
		"aec": "<strong><a href=\"https://avenirencommun.fr/le-livret-laicite/\">Abrogation de la Loi Debr</a></strong> pour mettre fin au dtournement de fonds publics attribus  lenseignement confessionnel priv, tout en respectant la  libert de lenseignement .<br/><br><strong><a href=\"https://laec.fr/s7m1\">tendre le bnfice de l&#39;application de la loi de 1905</a></strong>  tout le territoire de la Rpublique (abroger le concordat d&#39;Alsace-Moselle et les divers statuts spcifiques en vigueur dans les Outre-mer).<br/><br><strong><a href=\"https://laec.fr/s7m2\">Garantir la libert de conscience</a></strong> et l&#39;galit de toutes les options spirituelles devant la loi.<br/><br><strong><a href=\"https://laec.fr/s7m3\">Combattre tous les communautarismes</a></strong> et l&#39;usage politique des religions.<br/><br><strong><a href=\"https://laec.fr/s7m5\">Refuser de rencontrer d&#39;tat  tat ceux qui obligent nos ministres femmes  porter des accoutrements</a></strong> contraires  la dignit rpublicaine.<br/><br><strong><a href=\"https://laec.fr/s7m6\">Interdire la prsence de ministres et prfets aux crmonies religieuses</a></strong> et refuser le titre de chanoine de Latran pour le prsident de la Rpublique.",
