		"fn": " [97] Renforcer lunit de la nation par la promotion du roman national et le refus des repentances dtat qui divisent.<br> [103] Rtablir l&#39;autorit et le respect du maitre et instaurer le port d&#39;un uniforme  l&#39;cole<br> [81] Revaloriser le travail manuel par ltablissement de filires professionnelles dexcellence (suppression progressive du collge unique, autorisation de lapprentissage  partir de 14 ans). Dvelopper des lyces professionnels ou technologiques de la seconde chance sur tout le territoire pour les lves sortis du systme scolaire sans diplme.<br> [106]  luniversit, passer dune slection par lchec  une slection au mrite. Refuser le tirage au sort comme moyen de slection. Revaloriser les bourses au mrite. Dfendre le modle denseignement suprieur franais, qui passe par la complmentarit de luniversit et des grandes coles.",
		"fi": "Pour &quot;restaurer&quot; l&#39;autorit, le FN mise sur les <a href=\"https://www.collectifracine.fr/blog/2016/09/22/les-100-propositions-du-collectif-racine-pour-lecole-et-luniversite-de-demain/\">propositions du collectif Racine</a>, qui veut donner la priorit aux savoirs (le contenu des matires) plutt qu&#39;aux mthodes d&#39;accompagnement (la pdagogie). Le recentrage exclusif sur les savoirs dits fondamentaux, vieille lune de la droite ractionnaire, a toujours eu pour effet d&#39;exclure les plus dmunis, car tout le monde n&#39;a pas la possibilit de faire tudier son enfant le soir.</p>\n<p>Au-del des grandes dclarations, le programme du FN ne prvoit rien pour rduire les ingalits. Le principe de &quot;mritocratie personnelle&quot; semble bien illusoire. D&#39;ailleurs, en dehors d&#39;une hausse du budget de la recherche, il ne propose aucune augmentation des moyens, donc de l&#39;encadrement, pour assurer de meilleurs conditions de scolarisation. L&#39;accompagnement est, pour nous, la clef de la russite scolaire face aux dterminismes sociaux. L&#39;effectif par classe est essentiel : <a href=\"http://www.education.gouv.fr/cid195/les-chiffres-cles-du-systeme-educatif.html\">en 2016, les lves taient 25 par classe en collge, 19 en lyce professionnel et 30 en lyce gnral</a>. Le FN voudrait <a href=\"https://www.mediapart.fr/journal/france/230317/programme-du-fn-14-la-reforme-de-l-education-une-ode-un-passe-fantasme?onglet=full\">retrouver l&#39;cole des annes 1960</a> avec, par exemple, un <a href=\"http://www.unsa-education.com/spip.php?article743\">retour en arrire sur les mthodes pour apprendre  lire</a>, l&#39;instauration des uniformes  l&#39;cole et la transformation du collge en &quot;collge de dtermination&quot;, avec une orientation possible vers la voie professionnelle ds la cinquime  une organisation scolaire abandonne en 1975. L&#39;Histoire montre que ce systme sert principalement les intrts du monde du travail dans une relation utilitariste : former les plus en difficult pour travailler, et les meilleurs pour devenir l&#39;lite du pays. Elle permet aussi dvacuer la problmatique de lchec scolaire par le biais de la formation professionnelle rapide.</p>\n<p>Restreindre l&#39;accs  l&#39;Universit participe de cette vision litiste, et rduire les difficults de luniversit franaise au systme de slection est idiot. Le taux dchec en licence, en BTS et en DUT doit questionner au-del du mode daccs. C&#39;est en dveloppant plus de filires et en garantissant l&#39;accs pour tous  une formation suprieure, et aussi en allant vers une socit plus juste que nous prparerons l&#39;avenir du pays.",
		"aec": "<strong><a href=\"https://laec.fr/s73\">Recruter au moins 60 000 enseignants supplmentaires</a></strong> durant le quinquennat pour abaisser le nombre moyen dlves par classe, rtablir un vritable corps denseignants remplaants et engager un plan de titularisation des prcaires; <strong><a href=\"https://avenirencommun.fr/le-livret-education/\">Recruter</a></strong> 8 000 conseillers principaux dducation (CPE) et renforcer les quipes dassistants dducation (AED).<br><a href=\"https://laec.fr/s73m1\">tendre la scolarit obligatoire de 3  18 ans</a>, en adaptant les pdagogies et les parcours scolaires.<br><strong><a href=\"https://laec.fr/s73m2\">Rduire massivement les effectifs par classe</a></strong> : 20 lves maximum en maternelle, primaire, collge, lyce professionnel et technologique, 25 en lyce gnral.<br><a href=\"https://avenirencommun.fr/le-livret-education/\">Reconstituer les Rseau d&#39;aides spcialises aux lves en difficult</a> (RASED) pour atteindre 15 000 postes; Assurer un soutien scolaire gratuit par le service public dducation; Replacer les disciplines au coeur des apprentissages en rtablissant les heures disciplinaires; Renforcer les dispositions permettant la matrise de la culture crite.<br><strong><a href=\"https://avenirencommun.fr/le-livret-education/\">Garantir la gratuit</a></strong> des cantines, du transport, des sorties scolaires, des manuels et de matriels sans marques, des quipements et fournitures ; Abroger la rforme des rythmes scolaires.<br><br><strong><a href=\"https://laec.fr/s74m1\">Arrter les fermetures de lyces professionnels</a></strong> et augmenter le nombre d&#39;tablissements.<br><strong><a href=\"https://laec.fr/s27m1\">Verser une allocation dautonomie de 800  /mois</a></strong> sous condition de ressources pour les jeunes engages dans une formation qualifiante. Les bourses actuelles seront maintenues pour les cursus suprieurs  trois ans.<br><a href=\"https://laec.fr/s74m3\">Dvelopper les cursus courts dans l&#39;enseignement suprieur</a> et la reconnaissance complte des qualifications du suprieur dans les conventions collectives. Encourager la poursuite d&#39;tudes aprs un bac professionnel ou technologique (BTS, DUT, licences professionnelles).<br><a href=\"https://avenirencommun.fr/livret-de-lenseignement-de-recherche/\">Remise en cause du processus d&#39;autonomie des universits, mise en place de la gratuit</a> des universits ds la rentre 2017, et doublement du budget de lEnseignement Suprieur et de la Recherche en cinq ans.<br><a href=\"https://avenirencommun.fr/livret-de-lenseignement-de-recherche/\">Recrutement</a> de 5 000 chercheureuses, enseignantes-chercheureuses et personnels techniques en trois ans est un minimum, et titularisation des prcaires.",
