		"fn": " [65] Garantir la Scurit sociale pour tous les Franais ainsi que le remboursement de lensemble des risques pris en charge par lAssurance maladie(...).<br> [66] Relever le numerus clausus daccs aux tudes de sant pour viter le recours massif aux mdecins trangers (...).<br> [67] Lutter contre les dserts mdicaux en instaurant un stage dinternat dans les zones concernes, en permettant aux mdecins retraits dy exercer avec des dductions de charges et en y dveloppant les maisons de sant.<br> [68] Maintenir au maximum les hpitaux de proximit et augmenter les effectifs de la fonction publique hospitalire.<br> [69] Crer un cinquime risque de Scurit sociale consacr  la dpendance afin de permettre  chaque Franais de se soigner et de vivre dans la dignit.<br> [70] Soutenir les startups franaises pour moderniser le systme de sant.<br> [71] Raliser des conomies en supprimant lAide Mdicale dtat rserve aux clandestins, en luttant contre la fraude (...), en baissant le prix des mdicaments coteux (...), en dveloppant la vente  lunit des mdicaments remboursables (en imposant aux laboratoires ladaptation de leurs chanes de production).<br> [72] Protger la complmentarit du double systme de sant public et libral. Protger le maillage territorial des acteurs indpendants de la sant (pharmacies, laboratoires danalyses).<br> [73] Rorganiser et clarifier le rle et les obligations des agences de scurit sanitaire et alimentaire et assurer leur indpendance.",
		"fi": "Marine Le Pen veut supprimer l&#39;Aide Mdicale d&#39;Etat (AME) &quot;rserve aux clandestins&quot; pour faire des conomies. Cette aide <a href=\"http://www.lemonde.fr/les-decodeurs/article/2016/10/07/idee-recue-sur-les-migrants-3-6-ils-viennent-en-france-pour-se-faire-soigner_5009730_4355770.html\">est l&#39;quivalent de la CMU pour les plus dmunis en situation irrgulire</a>, et leur de bnficier de la gratuit des soins, dans <a href=\"https://www.service-public.fr/particuliers/vosdroits/F3079\">la limite des tarifs de la scurit sociale</a>. Les conditions d&#39;attribution ont dj t durcies en 1993 et en 2005: Il faut maintenant tre majeur, rsider en permanence sur le sol franais, et surtout tre en-dessous du seuil de pauvret (<a href=\"https://www.service-public.fr/particuliers/vosdroits/F3079\">9709 euros sur l&#39;anne</a>). L&#39;AME est un principe <a href=\"http://www.lemonde.fr/societe/article/2010/09/28/restreindre-l-acces-aux-soins-des-immigres-un-non-sens-en-terme-de-sante-publique_1417174_3224.html\">d&#39;urgence lmentaire</a>, qui <a href=\"http://www.alternatives-economiques.fr/sante-un-diagnostic-partage-remedes-differents/00078056\">limite la diffusion d&#39;pidmies</a>. Outre le caractre inhumain et xnophobe de cette mesure, la supprimer peut avoir de graves consquences sanitaires.</p>\n<p>Les mesures prvues par le FN contre les dserts mdicaux sont insuffisantes. Le numerus clausus a dj t relev trs significativement  <a href=\"https://www.legifrance.gouv.fr/eli/arrete/2017/1/10/AFSH1700585A/jo\">8 000</a>, contre 3 000 en 2015, et cette mesure n&#39;aura d&#39;effet qu&#39;une dizaine d&#39;annes aprs. Les internes en mdecine sont des praticiens en formation, qui n&#39;ont pas la formation ni l&#39;exprience des professionnels, et qui doivent aussi parfois travailler  ct pour financer leurs tudes. L&#39;incitation  l&#39;installation de mdecins dans les dserts mdicaux <a href=\"http://www.la-croix.com/France/Deserts-medicaux-secu-parie-lincitation-financiere-2016-09-01-1200785878\">existe dj, et n&#39;a pas montr son efficacit</a>, en plus de faire baisser les recettes de l&#39;Etat. Contre les dserts mdicaux, il faudrait <a href=\"http://www.alternatives-economiques.fr/sante-un-diagnostic-partage-remedes-differents/00078056\">notamment remettre en cause les dpassements d&#39;honoraires</a>, ce que ne prvoit pas le FN. Dvelopper les maisons de sant est une bonne chose, mais <a href=\"http://www.jim.fr/medecin/actualites/pro_societe/e-docs/louees_par_tous_les_candidats_les_maisons_de_sante_ne_seraient_pas_la_solution_miracle__164484/document_actu_pro.phtml\">certains professionnels n&#39;apprcient pas cette forme d&#39;organisation, et elles ont souvent des problmes de financement</a>, qui touchent aussi les professionnels qui y exercent.</p>\n<p>Rien n&#39;est prvu contre la <a href=\"http://www.liberation.fr/france/2016/11/06/l-hopital-malade-de-sa-logique-financiere_1526631\">logique de privatisation progressive des hpitaux et leur soumission  des objectifs de rentabilit</a>,  travers notamment la <a href=\"https://www.marianne.net/societe/t2a-l-hopital-public-une-situation-intenable-pour-les-soignants\">Tarification  l&#39;activit ( T2A )</a>. Le programme de M. Le Pen ne prvoit non plus aucun changement en terme de prvention. Cela montre que les problmes de sant publique n&#39;y sont pas pris au srieux.",
		"aec": "<strong><a href=\"https://laec.fr/section/67/faire-passer-la-sante-d-abord-et-pour-tous#mesure-1\">Rembourser  100% les soins de sant prescrits</a></strong>, dont les soins et appareils dentaires, optiques et auditifs, faire baisser les tarifs des lunettes et appareils auditifs.<br/><br><strong><a href=\"https://laec.fr/section/67/faire-passer-la-sante-d-abord-et-pour-tous#mesure-2\">Instaurer une politique de sant publique et de prvention</a></strong>: Plan de sant environnementale (contre la pollution, interdiction des pesticides); Implantation d&#39;un rseau sans faille de mdecine du travail; Eradication des maladies chroniques lies  l&#39;alimentation; Lutte contre le logement insalubre et le mal-logement; Renforcement de la mdecine scolaire et la Protection maternelle et infantile; Plan de lutte contre les maladies sexuellement transmissibles; <strong><a href=\"https://laec.fr/section/67/faire-passer-la-sante-d-abord-et-pour-tous#mesure-7\">Plan national de lutte contre la rsistance microbienne</a></strong> en faisant le bilan de l&#39;levage productiviste.<br/><br><strong><a href=\"https://laec.fr/section/67/faire-passer-la-sante-d-abord-et-pour-tous#mesure-3\">Combler les dserts mdicaux</a></strong> et crer un corps de mdecins gnralistes fonctionnaires rmunrs pendant leurs tudes, afin de pallier l&#39;insuffisance de mdecins dans certaines zones; <strong><a href=\"https://avenirencommun.fr/le-livret-sante/\">Dvelopper  massivement</a></strong>  les  centres  de  sant  locaux  et pluri-professionnels.<br/><br><strong><a href=\"https://laec.fr/section/67/faire-passer-la-sante-d-abord-et-pour-tous#mesure-4\">Abolir les dpassements d&#39;honoraires</a></strong>, renforcer le paiement au forfait.<br/><br><strong><a href=\"https://laec.fr/section/67/faire-passer-la-sante-d-abord-et-pour-tous#mesure-5\">Reconstruire le service public hospitalier</a></strong>, revenir sur la tarification  l&#39;acte et les suppressions de lits et de personnels, et engager un plan pluriannuel de <strong><a href=\"https://avenirencommun.fr/le-livret-sante/\">recrutement de 62 500 mdecins, infirmiers, aides-soignants et personnels administratifs</a></strong>.<br/><br><strong><a href=\"https://laec.fr/section/67/faire-passer-la-sante-d-abord-et-pour-tous#mesure-6\">Crer un ple public du mdicament</a></strong> pour faciliter l&#39;gal accs aux traitements, protger la recherche de la finance et supprimer l&#39;influence des entreprises prives dans les activits mdicales et hospitalires, notamment par l&#39;arrt de l&#39;accs libre des visiteurs mdicaux  l&#39;hpital public<br/><br> <strong><a href=\"https://avenirencommun.fr/le-livret-sante/\">Lutter contre les dpenses inutiles</a></strong> en dfinissant dmocratiquement un primtre de la solidarit de tous les biens et services mdicaux dont lutilit mdicale est reconnue.",
