		"intro": "Pour le Front National, progrs humain rime avec prservation de l&#39;identit nationale. Un identit reposant entre autre sur le  roman national  de l&#39;Histoire de France, qui ferait de cette Histoire <a href=\"http://www.leshistoriensdegarde.fr/preuve-source/introduction/\">une fiction</a>, dont  M.Le Pen voudrait mettre en avant<a href=\"http://la1ere.francetvinfo.fr/2013/02/28/marine-le-pen-pour-un-enseignement-des-aspects-positifs-de-la-colonisation-17719.html\"> les  lments les plus glorieux , comme les  aspects positifs  de la colonisation</a>.<br>La logique de repli le conduit  proposer des mesures discriminatoires et ingalitaires. Comme sur beaucoup de thmes, en matire de sant, le FN en profite pour s&#39;en prendre aux immigrs en voulant supprimer l&#39;Aide Medicale d&#39;Etat, qui d&#39;aprs Nicolas Bay, <a href=\"http://lelab.europe1.fr/Le-Front-national-s-empare-des-sans-dents-de-Francois-Hollande-16402\"> permet aux clandestins de ne pas tre des &quot;sans-dents&quot; </a>. Pour l&#39;ducation, M.Le Pen a dfendu la<a href=\"http://www.lefigaro.fr/elections/presidentielles/2016/12/08/35003-20161208ARTFIG00201-marine-le-pen-veut-restreindre-l-acces-des-enfants-d-etrangers-a-l-ecole-gratuite.php\"> fin de la scolarisation automatique et gratuite pour les enfants de clandestins</a>, mais a finalement retir cette mesure.<br>Dfendant toujours des solutions trs librales conomiquement, le FN veut mettre en place un contrle et un encadrement des soins, de l&#39;ducation, de la culture, sous prtexte de rpondre  des attentes lgitimes d&#39;largissement des droits. Mais il s&#39;agit bien d&#39;asservir ces domaines du progrs humain  l&#39;idologie d&#39;enfermement sur soi et de rejet des autres cultures, des autres civilisations qui font pourtant la richesse humaine.",
