		"image": "securite-justice-defense",
		"fn": " [118] Quitter le commandement militaire intgr de lOTAN pour que la France ne soit pas entrane dans des guerres qui ne sont pas les siennes<br> [120] Reconstituer dans tous les domaines de la Dfense une offre industrielle franaise pour rpondre aux besoins de nos armes et garantir notre indpendance stratgique.<br> [121] Augmenter ds la premire anne du mandat le budget de la Dfense  2 % du P.I.B. puis tendre vers 3 %  lhorizon de la fin du quinquennat<br> [121.1] Un second porte-avions baptis  Richelieu , indispensable  la permanence  la mer de notre groupe aronaval<br> [121.3] La prennisation de notre force de dissuasion nuclaire;<br> [121.4] Laugmentation gnrale de notre format darme (davantage davions, de navires, de blinds) et la modernisation des quipements ;",
		"fi": "Le FN veut acqurir un porte-avion <a href=\"http://www.liberation.fr/elections-presidentielle-legislatives-2017/2017/01/26/mais-combien-ca-coute-en-vrai-un-porte-avions_1544198\">dune valeur d&#39;environ 4,5 milliards deuros</a> et de nombreux quipements, alors que des <a href=\"http://www.latribune.fr/entreprises-finance/industrie/aeronautique-defense/le-prochain-fusil-de-l-armee-francaise-sera-allemand-598514.html\">commandes sont dj en cours</a>. Le parti promet une sortie de lOTAN mais adhre au principe du <a href=\"http://www.ladepeche.fr/article/2017/03/31/2547807-otan-tillerson-exige-europe-depense-plus-defense.html\"> au moins 2 % du P.I.B. pour la dfense de chaque pays membre </a> que cette organisation exige, et va mme bien plus loin. Amener le budget de la dfense  2% du PIB reprsenterait 7 milliards  de dpenses supplmentaires la 1re anne, pour monter  <a href=\"http://www.liberation.fr/elections-presidentielle-legislatives-2017/2017/05/01/comment-le-fn-deshabille-le-budget_1566602\">24 milliards/an d&#39;ici 2022</a> pour atteindre les 3%. Se pose alors le problme du financement, alors que <a href=\"http://analysonslefn.fr/themes/economie-finance-budget\">le budget prsent par le FN n&#39;a rien d&#39;quilibr</a>. De plus, il semble que le ministre de la Dfense ne soit <a href=\"http://www.latribune.fr/entreprises-finance/industrie/aeronautique-defense/marine-le-pen-promet-un-effort-de-defense-a-3-du-pib-en-2022-669351.html\">pas en capacit de dpenser autant d&#39;argent aussi vite</a>.</p>\n<p>Ces hausse spectaculaire du budget militaire entrainerait une militarisation massive du pays qui nous parat dangereuse. Nous ne savons pas vraiment au service de quelle politique trangre elle se ferait, puisque celle-ci est quasi inexistante avec seulement 3 des 144 propositions du programme. On peut donc s&#39;inquiter de l&#39;absence d&#39;une politique cohrente et d&#39;une vritable stratgie dans ce domaine.</p>\n<p>Larsenal nuclaire franais est dj <a href=\"http://www.lemonde.fr/les-decodeurs/article/2015/02/21/de-quoi-l-arsenal-nucleaire-de-la-france-est-il-compose_4580740_4355770.html\">important</a> et <a href=\"http://www.letelegramme.fr/france/nucleaire-la-bombe-en-sept-questions-03-11-2016-11278292.php\">constitu de centaines de bombes de 100kt</a>.  Son renouvellement augmente encore les risques d&#39;accidents nuclaires, qui ne sont  d&#39;ailleurs pas si rare puisque <a href=\"http://www.irenees.net/bdf_fiche-analyse-1049_fr.html\">toutes les puissances nuclaires ont dj fait face  un accident de ce type </a>, avec des <a href=\"http://www.irenees.net/bdf_fiche-analyse-1049_fr.html\">consquences terribles</a>.",
		"aec": "<strong><a href=\"https://laec.fr/s54m2\">Sortir de l&#39;Otan</a></strong> et refuser la participation de la France  toute alliance militaire permanente  l&#39;exception des oprations de maintien de la paix sous l&#39;gide de l&#39;Onu; <a href=\"https://avenirencommun.fr/livret-garde-nationale-defense/\">Retirer</a> la France de lEurope de la dfense ; <a href=\"https://avenirencommun.fr/livret-garde-nationale-defense/\">Mettre des moyens</a> pour former une force militaire onusienne permanente ; et de faire vivre le comit dtat-major de lONU pour commander les oprations de maintien de la paix ;<br><strong><a href=\"https://laec.fr/s55m1\">Rdiger un nouveau Livre blanc</a></strong> et adopter une nouvelle loi de programmation militaire ayant pour objectif la restauration pleine et entire de l&#39;indpendance militaire.<br><strong><a href=\"https://laec.fr/s55m2\">Stopper les programmes de privatisation</a></strong> des industries d&#39;armement et des missions de dfense nationale et engager leur reconqute publique  ; tablir la rgle de<a href=\"https://laec.fr/s55m3\"> l&#39;acquisition de matriel militaire franais</a> par l&#39;arme (armement et fournitures).<br><strong><a href=\"https://laec.fr/s60m2\">Mettre en place une coalition universelle sous mandat de l&#39;Onu pour radiquer Daech</a></strong> et rtablir la paix et la stabilit en Syrie et en Irak, associant les combattants kurdes.<br><strong><a href=\"https://avenirencommun.fr/livret-garde-nationale-defense/\">De nouveaux investissements</a></strong> seront ncessaires, et un coup darrt immdiat sera port  la diminution des effectifs combattants professionnels. Le recrutement sera largi en compensation de ces coupes austritaires ; Ouverture dun <a href=\"https://avenirencommun.fr/livret-garde-nationale-defense/\">dbat sur le droit  la syndicalisation</a> des militaires.<br><strong><a href=\"https://avenirencommun.fr/livret-garde-nationale-defense/\">Fin de lopration Sentinelle</a></strong>, coteuse et inefficace dans la lutte contre le terrorisme, et  toute autre forme de militarisation de la scurit intrieure.<br>Ouvrir la possibilit d&#39;effectuer son <strong><a href=\"https://laec.fr/s55m4\">service citoyen obligatoire sous la forme d&#39;un service militaire</a></strong> de 9 mois rmunr au smic ; <a href=\"https://avenirencommun.fr/livret-garde-nationale-defense/\">Renouvellement de la Garde nationale</a> qui sera place sous commandement civil et mobilisable en cas de crise scuritaire, cologique et industrielle, affecte  la scurit des installations dimportance vitale pour la nation, et au renforcement des capacits de cyberdfense.<br><strong><a href=\"https://avenirencommun.fr/livret-garde-nationale-defense/\">Relancer les processus multilatraux de dsarmement nuclaire</a></strong> et conventionnel dans le cadre de la Confrence du dsarmement de lONU ; Mais la dissuasion nuclaire restera un lment de notre protection en labsence daccord de dsarmement multilatral.<br><strong><a href=\"https://avenirencommun.fr/livret-garde-nationale-defense/\">Mettre fin  la  Franafrique </a></strong> et rvision de nos accords militaires avec les pays africains.",
