		"image": "securite-justice-defense",
		"fn": " [17] Appliquer la tolrance zro et en finir avec le laxisme judiciaire par labrogation des lois pnales laxistes (comme la loi Taubira), le rtablissement des peines planchers et la suppression des remises de peine automatiques.<br> [18] Lutter contre la dlinquance des mineurs en responsabilisant les parents par la suppression du versement des aides sociales aux parents de mineurs rcidivistes en cas de carence ducative manifeste.<br> [19] Instaurer une peine de prison de perptuit relle incompressible pour les crimes les plus graves.<br> [20] Crer 40000 places supplmentaires de prison en 5 ans.<br> [21] Rtablir lexpulsion automatique des criminels et des dlinquants trangers. Mettre en place des accords bilatraux permettant que les trangers condamns purgent leur peine dans leur pays dorigine.<br> [22] Rattacher ladministration pnitentiaire au ministre de lIntrieur et renforcer le renseignement pnitentiaire.<br> [23] Augmenter le nombre de postes de magistrats, notamment par un recrutement au tour extrieur. Afin de rompre avec la culture du laxisme, supprimer lcole Nationale de la Magistrature et crer une filire de formation commune aux carrires judiciaires (avec des coles dapplication).",
		"fi": "La peine de prison  perptuit  relle  ou  incompressible  <a href=\"http://www.lemonde.fr/les-decodeurs/article/2016/01/12/la-perpetuite-reelle-existe-t-elle-en-france_4846127_4355770.html\">existe dj depuis 1994, pour certains cas</a> assez rares. La <a href=\"https://fr.wikipedia.org/wiki/R%C3%A9tention_de_s%C3%BBret%C3%A9_en_France\">rtention de sret</a> permet aussi la dtention  vie. Il y a actuellement <a href=\"http://www.justice.gouv.fr/art_pix/mensuelle_avril_2017.pdf\">un peu plus de 70 000 dtenu-e-s</a> en France, et 40 000 places supplmentaires serait une hausse norme de presque 60%. On peut donc craindre une certaine criminalisation de la socit avec cette doctrine de la  tolrance zro . Et s&#39;interroger sur le cot de la mesure, alors que Jean-Jacques Urvoas <a href=\"http://tempsreel.nouvelobs.com/justice/20160920.OBS8379/prisons-urvoas-promet-10-000-a-16-000-nouvelles-cellules.html\">demandait dj un peu plus d&#39;1 milliards pour crer moins de 7000 places</a>.</p>\n<p><a href=\"https://www.legifrance.gouv.fr/affichTexte.do?cidTexte=JORFTEXT000029362502&amp;categorieLien=id\">La  loi Taubira  de 2014</a> partait du principe que la peine judiciaire doit servir un but de rinsertion et d&#39;ducation des coupables. Elle tait aussi motive par une <a href=\"http://www.europe1.fr/societe/la-surpopulation-carcerale-en-cinq-chiffres-2816041\">densit carcrale trs leve</a>, 119% en 2016, jusqu&#39; 200%, avec parfois 3 ou 4 dtenus dans des cellules de 9 m. Mais parler de  laxisme  semble trs exagr quand il y a eu 4000 emprisonnements supplmentaires entre 2012 et 2017 (de <a href=\"http://www.lepoint.fr/chroniqueurs-du-point/laurence-neuer/la-prison-en-chiffres-27-10-2012-1521704_56.php\">plus de 66 000</a>   <a href=\"http://www.justice.gouv.fr/art_pix/mensuelle_avril_2017.pdf\">plus de 70 000</a>) , et que C. Taubira a fait voter la <a href=\"http://tempsreel.nouvelobs.com/justice/20160920.OBS8379/prisons-urvoas-promet-10-000-a-16-000-nouvelles-cellules.html\">construction de 6.500 places de prison</a>.</p>\n<p>Les parents de mineurs rcidivistes sont souvent dj pauvres, et les appauvrir encore plus ne les aidera certainement pas  mieux duquer leurs enfants, en plus de les stigmatiser et de nier la responsabilit de la socit. Passer des accords bilatraux avec chaque pays d&#39;origine des dlinquants serait long et fastidieux, alors que les dtenus trangers sont minoritaires (<a href=\"http://www.midilibre.fr/2014/06/04/justice-20-d-etrangers-en-prison-cible-de-discriminations,1001816.php\">moins de 20%</a>). Surtout, <a href=\"https://www.mediapart.fr/journal/france/230317/programme-du-fn-9-une-obsession-securitaire-tres-mal-renseignee\">la justice ne peut pas tre automatique</a> et les procdures dexpulsion et dextradition ne peuvent tre arbitraires, mais doivent tre examines au cas par cas.</p>\n<p>Rattacher l&#39;administration pnitentiaire au ministre de la justice remettrait en cause l&#39;indpendance de la justice. En dmocratie, la peine de prison est lie  une dcision de justice, et non  une dcision administrative. Les juges d&#39;application des peines et les conseillers dinsertion et de probation <a href=\"https://www.mediapart.fr/journal/france/230317/programme-du-fn-9-une-obsession-securitaire-tres-mal-renseignee\">dpendent aussi du ministre de la justice</a>. Le FN ne propose rien de prcis aussi pour remplacer l&#39;Ecole Nationale de la Magistrature, sans doute trop indpendante  son got.",
		"aec": "<strong><a href=\"https://laec.fr/s11m2\">Mettre fin  la pauprisation de la justice &amp; de l&#39;administration pnitentiaire</a></strong> :<br>-&nbsp;Mettre en uvre un plan de recrutement de personnels pour dsengorger les tribunaux,  en <a href=\"https://avenirencommun.fr/le-livret-justice/\">recrutant 18 000 magistrats, greffiers et personnels administratifs</a>.<br>-&nbsp;Rnover et construire de nouveaux locaux pour les tribunaux.<br>-&nbsp;Recruter 2000 agents pnitentiaires pour les escortes des dtenus.<br>-&nbsp;Mettre fin au tout-carcral par des peines alternatives  la prison, rnover les prisons pour garantir la dignit humaine, assurer la socialisation des dtenus.<br><strong><a href=\"https://laec.fr/s11m3\">Remplacer</a></strong> les instructions ministrielles au Parquet par des lois d&#39;orientation de politique pnale, dbattues et votes par le Parlement.<br><strong><a href=\"https://laec.fr/s11m4\">Remplacer le Conseil suprieur de la magistrature par un Conseil suprieur de la justice</a></strong> dsign pour partie par les magistrats et le Parlement, devant lequel il sera responsable.<br><strong><a href=\"https://avenirencommun.fr/le-livret-justice/\">Supprimer la Cour de justice de la Rpublique</a></strong>, juridiction dexception en charge de juger de la responsabilit pnale des ministres (pour les actes commis dans le cadre de leurs fonctions).<br><strong><a href=\"https://avenirencommun.fr/le-livret-justice/\">Supprimer</a></strong> la possibilit pour les magistrates de passer du parquet au  sige , cest--dire dtre procureur puis juge ; <a href=\"https://avenirencommun.fr/le-livret-justice/\">Renforcement de lindpendance des juges dinstruction</a> par la collgialit et le dtachement dofficiers de police judiciaire auprs des juridictions, sur la base du volontariat et pour une priode de trois ans renouvelable.<br><strong><a href=\"https://avenirencommun.fr/le-livret-justice/\">Renforcer</a></strong> la lutte contre la corruption, la dlinquance en  col blanc , les infractions sanitaires, environnementales et de droit pnal du travail.<br><strong><a href=\"https://laec.fr/s11m1\">Garantir</a></strong> la protection judiciaire de la jeunesse, la justice des mineurs, la politique de prvention de la dlinquance et les sanctions ducatives.<br><strong><a href=\"https://avenirencommun.fr/le-livret-justice/\">Priorit donne aux mesures alternatives  lemprisonnement</a></strong> et aux services pnitentiaires dinsertion et de probation en charge de leur suivi.<br><strong><a href=\"https://avenirencommun.fr/le-livret-justice/\">Abolir la rtention de sret</a></strong>, qui consiste  placer un criminel dans un centre de sret aprs sa peine de prison sur la base dun suppos  risque  de dangerosit.",
