		"image": "securite-justice-defense",
		"pos": 2,
		"intro": "40 000 places de prison supplmentaires, 15 000 policiers et gendarmes, un budget militaire  3% du PIB.... Dans les domaines de la scurit, de la dfense et de la justice, le Front National cherche   cultiver une image de force et de fermet totale,  l&#39;oppos d&#39;un prtendu  laxisme  gnral.<br>Dans le domaine de la scurit, il mise sur le tout-rpressif et la  tolrance zero , et M. Le Pen s&#39;est mme dclare <a href=\"http://www.lexpress.fr/actualite/politique/fn/pour-marine-le-pen-l-etat-d-urgence-aujourd-hui-c-est-de-la-com-du-bidon_1793987.html\">pour le renversement de la charge de la preuve en matire de lgitime dfense</a>. Un policier ayant tu quelqu&#39;un lors d&#39;une intervention naurait alors plus  prouver quil tait en situation de lgitime dfense, et ce serait aux enquteurs de dmontrer linverse. Dans la lutte anti-terroriste, le parti focalise son attention sur les fichs S et les trangers, sans vritablement s&#39;attaquer aux sources du terrorisme international. Concernant la justice, M. Le Pen ne cesse de dnoncer un prtendu <a href=\"https://www.franceculture.fr/droit-justice/fillon-le-pen-emmanuelli-25-ans-de-rengaine-politique-contre-le-gouvernement-des-juges\"> gouvernement des juges </a>, mais la justice risque d&#39;tre encore moins indpendante avec le FN. Et en matire de dfense, ce parti veut engager le pays dans une trs coteuse course  l&#39;armement militaire, dans un but trs flou.",
