		"intro": "Depuis la crise du libralisme conomique de 2008, qui a servi de prtexte  la prcarisation des travailleurs et  l&#39;abaissement des droits sociaux, <a href=\"http://bfmbusiness.bfmtv.com/france/comment-le-fn-est-passe-de-l-ultra-liberalisme-a-l-etat-protecteur-933363.html\">le Front National s&#39;est mis  prner un Etat fort</a>, face  la mondialisation et la drgulation. Si les problmatiques sociales semblent importantes dans les discours du FN, dans le programme, elles le sont beaucoup moins. Malgr un chmage toujours proche de 10%, une flexibilisation et une prcarisation du travail acclre par la loi El Khomri, l&#39;emploi semble tre un problme secondaire pour ce parti. Le terme  chmage  n&#39;apparait mme pas dans son programme, et  prcarit  seulement deux fois (une pour le droit des femmes, et l&#39;autre pour la  prcarit nergtique ). Dans ce domaine, comme en matire de logement, de retraites ou de pouvoir d&#39;achat, le FN dfend la <a href=\"https://analysonslefn.fr/themes/immigration-laicite-priorite-nationale\"> priorit nationale </a> et pense que  le problme de l&#39;immigration  est prioritaire. Tout en dfendant des solutions au fond trs librales, accompagnes de quelques mesures sociales finalement trs rduites.",
