<template>
  <main class="alp-home">
    <h1 class="alp-home__title">
      Analysons le projet du <span class="alp-home__title__red">Front National</span>
    </h1>
    <p class="alp-home__intro">
      Les rapprochements et amalgames entre le programme de la France Insoumise et celui du Front National ont t trs nombreux et trs virulents. Pourtant, <strong>aucune analyse srieuse et critique des propositions de Marine Le Pen</strong> n'a t vraiment ralise pour tenter de les confronter  celles portes par Jean-Luc Mlenchon.
      <br>
      <br>
      Le Discord Insoumis propose donc une analyse des propositions du Front National  travers plusieurs thmes. Cette analyse est bien videmment trs critique car le projet de socit de Marine Le Pen est non seulement  l'oppos de celui de la France Insoumise mais il prsente de nombreuses incohrences.
      <br>
      <br>
      L'objectif de ce site est d'tre <strong>une source de rflexion</strong> pour favoriser l'esprit critique de la population autour du projet mortifre du Front National. C'est un espace en dveloppement permanent qui sera enrichi au fil des prochaines semaines.
    </p>
    <p class="alp-home__choose">
      Slectionnez un thme pour analyser le projet du
      <span class="alp-home__intro__red">Front National</span>
    </p>

    <img class="alp-home__icon" src="./assets/arrow.down.png" alt="arrow down" height="24" width="24">

    <div class="alp-home__themes">
      <alp-big-theme
        v-for="theme in themes"
        key="theme.slug"
        :link="getLink(theme)"
        :image="theme.slug">{{ theme.title }}</alp-big-theme>
    </div>

    <div class="alp-home__footer">
      Site  l'initiative de citoyens insoumis.
    </div>
  </main>
</template>

<script>
import themes from './themes.json'

import Button from './Button.vue'
import BigTheme from './BigTheme.vue'

export default {
  components: {
    'alp-button': Button,
    'alp-big-theme': BigTheme
  },

  computed: {
    themes() {
      return Object.keys(themes)
        .map(themeName => themes[themeName])
        .filter(theme => !theme.invisible && theme.main)
        .sort((a, b) => a.pos - b.pos)
    }
  },

  methods: {
    getLink(theme) {
      return theme.subs ? theme.subs[0].link : theme.slug
    }
  }
}
</script>

<style>
.alp-home__title {
  color: #23b9d0;
  font-size: 36px;
  font-weight: 600;
  margin: 80px 0 30px 0;
  text-align: center;
}

.alp-home__title__red {
  color: #c9462c;
}

.alp-home__intro {
  color: #222222;
  font-size: 18px;
  margin: 0 auto;
  max-width: 700px;
  padding: 30px 10px;
  text-align: justify;
}

.alp-home__choose {
  color: #23b9d0;
  margin: 0 auto;
  max-width: 700px;
  text-align: center;
  text-transform: uppercase;
}

.alp-home__intro__red {
  color: #c9462c;
  font-weight: 600;
}

.alp-home__icon {
  display: block;
  margin: 20px auto;
}

.alp-home__themes {
  margin: 80px auto 30px auto;
  max-width: 1200px;
  text-align: center;
  padding: 0 10px;
  overflow: auto;
  zoom: 1;
}

.alp-home__themes .alp-big-theme-wrapper {
  float: left;
  display: block;
  margin-bottom: 10px;
  width: calc(33.3333333% - (20px / 3));
}

.alp-home__themes .alp-big-theme-wrapper:not(:nth-child(3n)) {
  margin-right: 10px;
}

.alp-home__footer {
  background: #ededed;
  color: #444;
  padding: 10px;
}

@media (max-width: 768px) {
  .alp-home__themes {
    flex-direction: column;
    height: 900px;
  }

  .alp-home__themes .alp-big-theme-wrapper {
    width: 100%;
  }

  .alp-home__themes .alp-big-theme-wrapper:not(:nth-child(3n)) {
    margin-right: 0;
  }
}
</style>



